.burger-menu {
  width: 50px;
  height: 30px;
  position: fixed;
  top: 30px;
  right: 15px;
  z-index: 110;
  border: none;
  background-color: transparent;
  cursor: pointer;
  outline: none; }
  @media (min-width: 992px) {
    .burger-menu {
      display: none; } }
  .burger-menu:hover .burger-menu__lines,
  .burger-menu:hover .burger-menu__lines::before,
  .burger-menu:hover .burger-menu__lines::after {
    background-color: #6a696b; }

.burger-menu__lines,
.burger-menu__lines::before,
.burger-menu__lines::after {
  display: block;
  margin: 0 auto;
  width: 30px;
  height: 3px;
  background-color: #2b2a2c;
  position: relative; }

.burger-menu__lines::before {
  content: "";
  position: absolute;
  top: -7px;
  transition: transform 0.2s ease-in, top 0.2s linear 0.2s; }

.burger-menu__lines::after {
  content: "";
  position: absolute;
  top: 7px;
  transition: transform 0.2s ease-in, top 0.2s linear 0.2s; }

.burger-menu--active .burger-menu__lines {
  background-color: transparent; }

.burger-menu--active .burger-menu__lines::before {
  top: 0;
  transform: rotate(45deg);
  transition: top 0.2s linear, transform 0.2s ease-in 0.2s; }

.burger-menu--active .burger-menu__lines::after {
  top: 0;
  transform: rotate(-45deg);
  transition: top 0.2s linear, transform 0.2s ease-in 0.2s; }

.burger-menu--active:hover .burger-menu__lines {
  background-color: transparent; }

